import React, { useState, useRef } from 'react'
import styled from 'styled-components'
import { fromEvent, FileWithPath } from 'file-selector'
import { SupportVideoType } from 'sdk'
import { Card, CardBody, Flex, Radio, Text, IconButton, FolderOpen } from 'uikit'
import { MediaType, NftMeta, MaxImageSize, MaxImageSizeString, MaxVideoSize, MaxVideoSizeString } from 'sdk/nft'
import useToast from 'hooks/useToast'
import { useTranslation } from 'contexts/Localization'
import { useActiveWeb3React } from 'hooks'
import FoldableInput from 'components/FoldableInput'
import FoldableHeader from 'components/FoldableHeader'
import { ogModule } from 'state/types'
import { getBalanceNumber } from 'utils/formatBalance'
import CreateActions from './CreateActions'

const Wrapper = styled(Flex)`
  margin-top: 2px;
  width: 500px;
  max-width:500px;

  ${({ theme }) => theme.mediaQueries.md} {
    flex: 1;
    margin-top: 0px;
  }

  ${({ theme }) => theme.mediaQueries.xl} {
    flex: 2;
  }
  `

const StyledInput = styled.input`
  width: 420px;  
  position: relative;
  display: flex;
  padding: 10px;
  align-items: center;
  white-space: nowrap;
  background: none;
  border: none;
  outline: none;
  border-radius: 12px;
  color: ${({ theme }) => theme.colors.text};
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
  -webkit-appearance: none;

  font-size: 14px;

  ::placeholder {
    color: ${({ theme }) => theme.colors.textDisabled};
  }
  transition: border 100ms;
  :focus {
    outline: none;
  }
`

const StyledTextArea = styled.textarea`
  width: 420px;  
  max-width:420px;
  min-width:420px;
  min-height:120px;
  height: 120px;  
  position: relative;
  display: flex;
  padding: 10px;
  align-items: center;
  background: none;
  border: none;
  outline: none;
  border-radius: 12px;
  color: ${({ theme }) => theme.colors.text};
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
  -webkit-appearance: none;

  font-size: 14px;

  ::placeholder {
    color: ${({ theme }) => theme.colors.textDisabled};
  }
  transition: border 100ms;
  :focus {
    outline: none;
  }
`
const StyledCardbody = styled(CardBody)`
  div:first-child {
    margin-top: 0px;
  }
`
const StyledCard = styled(Card)`
  max-width: 500px;
  margin: 0 8px 10px;
  display: flex;
  flex-direction: column;
  align-self: baseline;
  position: relative;
  box-shadow: 0px 1px 4px rgba(25, 19, 38, 0.15);

  ${({ theme }) => theme.mediaQueries.sm} {
    margin: 0 12px 10px;
  }
`
const StyledImage = styled.img`
  width:380px;
  height:380px;
  border-radius: 20px;
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
`

const StyledVideo = styled.video`
  width:380px;
  height:380px;
  border-radius: 20px;
  border-style: solid;
  border: 1px solid ${({ theme }) => theme.colors.primary};
`

interface CreateProps {
  og: ogModule
}

const CreateCard : React.FC<CreateProps> = ({ og }) => {
    const { t } = useTranslation()

    const { account } = useActiveWeb3React()

    const {createFee, payToken} = og

    const inputImageFile = useRef(null) 

    const inputVideoFile = useRef(null) 

    const [mediaTypeRadio, setmediaTypeRadio] = useState(MediaType.Image)

    const [selectedImageFileUrl, setSelectedImageFileUrl] = useState(null)

    const [selectedVideoFileUrl, setSelectedVideoFileUrl] = useState({ mkvurl: '', mp4url: '' })

    const [nameText, setNameText] = useState('')

    const [descText, setDescText] = useState('')

    const [selectFile, setSelectFile] = useState(null)

    const { toastError } = useToast()

    const onMediaChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      setmediaTypeRadio(e.target.value as MediaType)
    }

    function createMediaType()
    {
      return (
       <Flex justifyContent="space-between" alignItems="center">
         <Radio scale="sm" name={MediaType.Image} value='Image' checked={mediaTypeRadio=== MediaType.Image} onChange={onMediaChange}/><Text ml='5px' mr='36px' fontSize="16px" fontWeight="Bold">{t('Image')}</Text>
         <Radio scale="sm" name={MediaType.Video} value='Video' checked={mediaTypeRadio=== MediaType.Video} onChange={onMediaChange}/><Text ml='5px' fontSize="16px" fontWeight="Bold">{t('Video')}</Text>
       </Flex>
      )
    }

    function onOpenFile()
    {
      if(mediaTypeRadio === MediaType.Image)
      {
        inputImageFile.current.accept = "image/*" 

        inputImageFile.current.click()
      }

      else if(mediaTypeRadio === MediaType.Video)
      {
        inputVideoFile.current.accept = "video/mp4, video/x-matroska"

        inputVideoFile.current.click()
      }
    }

    function onChangeImageFile(event)
    {
       fromEvent(event).then(files=>
       {
         const plainFiles = files as FileWithPath[]

         if(plainFiles.length > 0)
         {
           const file = plainFiles[0]

           if(file.size > MaxImageSize)
           {
             toastError(
               `${t('Error')}`,
               `${t(`Exceed Max Image Size Limit, Max Image Size is ${MaxImageSizeString}`)}`,
             )     
           }
           else 
           {
             setSelectFile(event.target.files[0])
               
             const _URL = window.URL || window.webkitURL 
          
             const fileURL = _URL.createObjectURL(file)
  
             setSelectedImageFileUrl(fileURL)    
           }
        }
       })
    }

    function onChangeVideoFile(event)
    {
      fromEvent(event).then(files=>
        {
          const plainFiles = files as FileWithPath[]
 
          if(plainFiles.length > 0)
          {
            const file = plainFiles[0]

            if(file.size > MaxVideoSize)
            {
              toastError(
                `${t('Error')}`,
                `${t(`Exceed Max Video Size Limit, Max Video Size is ${MaxVideoSizeString}`)}`,
              )     
            }
            else 
            {
              const _URL = window.URL || window.webkitURL 
           
              const fileURL = _URL.createObjectURL(file)

              const videourls = { mkvurl: '', mp4url: '' }

              if(file.type === SupportVideoType.mkv)
              {
                videourls.mkvurl = fileURL

                setSelectedVideoFileUrl(videourls)    

                setSelectFile(event.target.files[0])
              }

              else if (file.type === SupportVideoType.mkv)
              {
                videourls.mp4url = fileURL

                setSelectedVideoFileUrl(videourls)    

                setSelectFile(event.target.files[0])
              }

              else 
              {
                toastError(
                  `${t('Error')}`,
                  `${t(`Unsupported Video Type`)}`,
                )
            }
          }
        }
        })
    }

    const onNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      setNameText(e.target.value)
    }

    const onDescChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      setDescText(e.target.value)
    }

    function getMeta(): NftMeta & { file: any }
    {
      const meta = {
        mediaType: mediaTypeRadio,

        name: nameText,
    
        desc: descText,

        mp4url: '',

        mkvurl: '',

        imageurl: '',

        file: selectFile
      }

      if(mediaTypeRadio === MediaType.Image)
      {
        meta.imageurl = selectedImageFileUrl
      }

      else if(mediaTypeRadio === MediaType.Video)
      {
        meta.mkvurl = selectedVideoFileUrl.mkvurl

        meta.mp4url = selectedVideoFileUrl.mp4url
      }

      return meta
    }

    function onCommitSuccess()
    {
      setSelectedImageFileUrl('')

      setSelectedVideoFileUrl({ mkvurl: '', mp4url: '' })

      setNameText('')

      setDescText('')
  
      setSelectFile(null)
    }

    const [createActionRefresh, setCreateActionRefresh] = useState(0)

    function onApproveSuccess()
    {
      setCreateActionRefresh(createActionRefresh + 1)
    }
  
    return (
      <Wrapper>
        <StyledCard>
          <StyledCardbody>
            <FoldableHeader header={createMediaType()} mt="10px">
              <Flex justifyContent="space-between" alignItems="center">
              <IconButton scale="sm" onClick={onOpenFile}>
                <FolderOpen />
              </IconButton>
              <Flex flexDirection="column">
              { mediaTypeRadio === MediaType.Image ?
                (<StyledImage src={selectedImageFileUrl}/>)
                :
                (<StyledVideo src={selectedVideoFileUrl.mkvurl} autoPlay controls loop >
                   <source src={selectedVideoFileUrl.mkvurl} type="video/webm" />
                   <source src={selectedVideoFileUrl.mp4url} type="video/mp4" />
                </StyledVideo>)
              }

              <input onChange={onChangeImageFile.bind(this)} type='file' id='file' ref={inputImageFile} style={{display: 'none'}}/>
              <input onChange={onChangeVideoFile.bind(this)} type='file' id='file' ref={inputVideoFile} style={{display: 'none'}}/>
              </Flex>

              </Flex>
            </FoldableHeader>

            <FoldableInput caption={t('Name')} isNecessary mt="10px">
               <StyledInput value={nameText} placeholder={t('')} onChange={onNameChange} type='text' maxLength={50}/>
            </FoldableInput>      

            <FoldableInput caption={t('Description')} isNecessary mt="10px">
               <StyledTextArea value={descText} onChange={onDescChange} placeholder={t('')}/>
            </FoldableInput>    

            <FoldableInput caption={t('Create Fee')} isNecessary={false} mt="10px">
             <Text fontSize="14px" ml='12px' color="textSubtle">
               {`${getBalanceNumber(createFee, payToken.decimals)} ${payToken.symbol}`}
             </Text>
            </FoldableInput>

            <CreateActions version={createActionRefresh} account={account} getMeta={getMeta} onApproveSuccess={onApproveSuccess} onCommitSuccess={onCommitSuccess}/>
          </StyledCardbody>
        </StyledCard>
      </Wrapper>
    )
  }
  
  export default CreateCard