import React from 'react'
import {
  ButtonMenu,
  ButtonMenuItem,
  Flex
} from 'uikit'
import { useTranslation } from 'contexts/Localization'

const OrdinaryGalleryTab = ({ showView, setShowView }) => {
  const { t } = useTranslation()

  return (
    <Flex alignItems="center" justifyContent="center" mb="10px">
      <Flex alignItems="center" flexDirection={['column', null, 'row', null]}>
        <ButtonMenu activeIndex={showView ? 0 : 1} onItemClick={(index) => setShowView(index === 0)} scale="sm" variant="subtle">
          <ButtonMenuItem>
            {t('View')}
          </ButtonMenuItem>

          <ButtonMenuItem>
            {t('Create')}
          </ButtonMenuItem>
        </ButtonMenu>
      </Flex>
    </Flex>
  )
}

export default OrdinaryGalleryTab
