import React, { useState } from 'react'
import { Button, Modal, Flex, Text } from 'uikit'
import { getENVChainId } from 'sdk'
import { ogPaths, NftMeta, MediaType } from 'sdk/nft'
import { V1 } from 'utils/uuid'
import { useTranslation } from 'contexts/Localization'
import { uploadBuffer, uploadFile } from 'hooks/ossClient'
import getOGConfig from 'sdk/nft/og'
import useOGMint from 'hooks/useOGMint'

interface CreateModalProps {
  getMeta: () => NftMeta & { file: any },

  onDismiss?: () => void,

  onSuccess?: () => void
}

const CreateModal: React.FC<CreateModalProps> = ({ getMeta, onDismiss, onSuccess }) => {
  const [pendingTx, setPendingTx] = useState(false)

  const [commitResult, setCommitResult] = useState({ step: 'step 0', message: 'waiting for commit', success: true })

  const { t } = useTranslation()

  const meta = getMeta()

  const metaKey = V1()

  const ogConfig = getOGConfig()

  function commitMeta()
  {
    setPendingTx(true)

    const metaContent = {
      name: meta.name,
  
      desc: meta.desc,
  
      mediaType: meta.mediaType,
  
      imageurl: meta.imageurl && meta.imageurl.length > 0 ? metaKey : '',
  
      mkvurl: meta.mkvurl && meta.mkvurl.length > 0 ? metaKey : '',
  
      mp4url: meta.mp4url && meta.mp4url.length > 0 ? metaKey : '',
    }

    setCommitResult({
      success: true,

      step: "step 1",

      message: "commit nft meta"
    })

    uploadBuffer(`${ogConfig.baseurl}${metaKey}`, Buffer.from(JSON.stringify(metaContent)), commitMetaCallback)
  }

  function commitMetaCallback(success: boolean, error?: string)
  {
     if(success)
     {
       commitNftContent()
     }

     else 
     {
      setCommitResult({
        success: false,
  
        step: "step 1",
  
        message: (error || "commit nft meta failed")
      })

      setPendingTx(false)
     }
  }

  function getNftContent() : { url: string, file: string }
  {
     const result = {
      url: '', 
      
      file: undefined
     }

     if(meta.mediaType === MediaType.Image)
     {
       result.url = `${ogPaths.basePath}/${ogPaths.imagePath}/${getENVChainId()}/`

       result.file = meta.file
     }

     else if(meta.mediaType === MediaType.Video)
     {
      result.url = `${ogPaths.basePath}/${ogPaths.videoPath}/${getENVChainId()}/`

      result.file = meta.file
     }

     return result
  }

  function commitNftContent()
  {
    setCommitResult({
      success: true,

      step: "step 2",

      message: "commit nft content"
    })

    const nftContent = getNftContent()

    uploadFile(`${nftContent.url}${metaKey}`, nftContent.file, commitNftContentCallback)
  }

  function commitNftContentCallback(success: boolean, error?: string)
  {
     if(success)
     {
       handleOnMint()
     }

     else 
     {
      setCommitResult({
        success: false,
  
        step: "step 2",
  
        message: (error || "commit nft content failed")
      })
      
      setPendingTx(false)
     }
  }

  const mintCallback = () =>
  {
    setPendingTx(false)

    setCommitResult({
      success: true,

      step: "end",

      message: "success"
    })

    if(onSuccess)
    {
      onSuccess()
    }

    onDismiss()
  }

  const [ onMint ] = useOGMint(metaKey, mintCallback)

  const handleOnMint = async () => {
      try {
        setCommitResult({
          success: true,
    
          step: "step 3",
    
          message: "mint nft token"
        })

        await onMint()
      } catch (e) {
        setPendingTx(false)

        setCommitResult({
          success: false,
    
          step: "step 3",
    
          message: e.message
        }) 
      }
  }

  return (
    <Modal title={t('Commit')} onDismiss={onDismiss} >
      <Flex maxWidth='420px' minWidth='420px' alignItems="center" flexDirection="column" justifyContent="space-between" mb="8px">
        <Text bold color="textSubtle" fontSize="18px" pr="3px">
         {t(commitResult.step)}
        </Text>
        { commitResult.success ? (<Text color="textSubtle" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)}
        </Text>) :
        (<Text bold color="failure" fontSize="14px" pr="3px" mt='10px'>
         {t(commitResult.message)} 
        </Text> )}
      </Flex>

      <Button
        isLoading={pendingTx}
        onClick={commitMeta}
        mt="24px"
      >
        {pendingTx ? t('Committing') : t('Commit')}
      </Button>
    </Modal>
  )
}

export default CreateModal
