function maxFromBits(bits) {
    return 2 ** bits
}

function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min
}

function randomUI04() {
    return getRandomInt(0, maxFromBits(4) -1)
}

function randomUI06() {
    return getRandomInt(0, maxFromBits(6) -1)
}

function randomUI08() {
    return getRandomInt(0, maxFromBits(8) -1)
}

function randomUI10() {
    return getRandomInt(0, maxFromBits(10) -1)
}

function randomUI12() {
    return getRandomInt(0, maxFromBits(12) -1)
}

function randomUI14() {
    return getRandomInt(0, maxFromBits(14) -1)
}

function randomUI16() {
    return getRandomInt(0, maxFromBits(16) -1)
}

function fromParts(timeLow, timeMid, timeHiAndVersion, clockSeqHiAndReserved, clockSeqLow, node) {
    const hex = `${timeLow.toString(16)}-${timeMid.toString(16)}-${timeHiAndVersion.toString(16)}-${clockSeqHiAndReserved.toString(16)}${clockSeqLow.toString(16)}-${node.toString(16)}`

    return hex
}

function getTimeFieldValues(time) {
    const ts = time - Date.UTC(1582, 9, 15);

    const hm = ((ts / 0x100000000) * 10000) & 0xFFFFFFF;

    return { low: ((ts & 0xFFFFFFF) * 10000) % 0x100000000,
              mid: hm & 0xFFFF, hi: hm >>> 16, timestamp: ts };
 }

export function V4() {
    return fromParts(
      randomUI16(),
      randomUI12(),
      0x4000 | randomUI14(),
      0x80   | randomUI06(),
      randomUI08(),
      randomUI10()
    )
  }
  
 export function V1() {
    const now = new Date().getTime();

    let sequence = randomUI14();

    const node = (randomUI08() | 1) * 0x10000000000 + randomUI16();

    let tick = randomUI04();

    let timestamp = 0;

    const timestampRatio = 1/4;
  
    if (now !== timestamp) {
      if (now < timestamp) {
        sequence++;
      }
      timestamp = now;
      tick = randomUI04();
    } else if (Math.random() < timestampRatio && tick < 9984) {
      tick += 1 + randomUI04();
    } else {
      sequence++;
    }
  
    const tf = getTimeFieldValues(timestamp);

    const tl = tf.low + tick;

    const thav = (tf.hi & 0xFFF) | 0x1000;
  
    const value = sequence & 0x3FFF;

    const cshar = (value >>> 8) | 0x80;

    const csl = value & 0xFF;
  
    return fromParts(tl, tf.mid, thav, cshar, csl, node);
  }