import React, { useState, useMemo, useRef, useEffect } from 'react'
import styled from 'styled-components'
import { Heading } from 'uikit'
import { orderBy } from 'lodash'
import { useTranslation } from 'contexts/Localization'
import { useOG } from 'state/hooks' 
import usePersistState from 'hooks/usePersistState'
import Page from 'components/layout/Page'
import FlexLayout from 'components/layout/Flex'
import CreateCard from './components/CreateCard'
import OrdinaryGalleryTab from './OrdinaryGalleryTab'
import OGNftGrid from './components/OGNftGrid'
import OGNftCard from './components/OGNftCard'

const StyledHeader = styled.div`
  border-bottom: 2px solid ${({ theme }) => theme.colors.textSubtle};
  margin-bottom: 12px;
  padding-bottom: 22px;
`
const ControlContainer = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  position: relative;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 20px;

  ${({ theme }) => theme.mediaQueries.sm} {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 16px 20px;
    margin-bottom: 0;
  }
`
const OrdinaryGallery : React.FC = () => {
  const { t } = useTranslation()

  const og = useOG()

  const [showView, setShowView] = usePersistState(true, 'grampus_og_view') 

  const nfts = useMemo(()=> og.usernfts || [], [og])
   
  const NUMBER_OF_NFT_VISIBLE = 12

  const loadMoreRef = useRef<HTMLDivElement>(null)

  const [numberOfNFTVisible, setNumberOfNFTVisible] = useState(NUMBER_OF_NFT_VISIBLE)

  const [observerIsSet, setObserverIsSet] = useState(false)

  const nftMemoized = useMemo(() => {
    const sortNFTS = (ntfs: number[]): number[] => {
        return orderBy(ntfs, (nft: number) => nft, 'desc')
    }

    return sortNFTS(nfts).slice(0, numberOfNFTVisible)
  }, [
    nfts,
    numberOfNFTVisible,
  ])

  useEffect(() => {
    const showMoreNFTs = (entries) => {
      const [entry] = entries

      if (entry.isIntersecting) {
        setNumberOfNFTVisible((nftCurrentlyVisible) => nftCurrentlyVisible + NUMBER_OF_NFT_VISIBLE)
      }
    }

    if (!observerIsSet) {
      const loadMoreObserver = new IntersectionObserver(showMoreNFTs, {
        rootMargin: '0px',

        threshold: 1,
      })

      loadMoreObserver.observe(loadMoreRef.current)

      setObserverIsSet(true)
    }
  }, [nftMemoized, observerIsSet])

  const handleRefresh = () => {
    alert('success!')
  }

  const renderContent = (): JSX.Element => {
    return (
      <div>
          { showView ? 
            <OGNftGrid>
              {nftMemoized.map((nft) => {
            return (
              <div key={nft}>
                <OGNftCard nftid={nft} refresh={handleRefresh} />
             </div>
            )
            })} 
            </OGNftGrid>
         : 
         <FlexLayout>
             <CreateCard og={og}/>
         </FlexLayout>
         }

      </div>
    )
  }

  return (
    <Page>
      <StyledHeader>
        <Heading as="h1" scale="xxl" color="secondary" mb="24px">
          {t('Ordinary Gallery')}
        </Heading>
      </StyledHeader>
      
      <ControlContainer>
         <OrdinaryGalleryTab showView={showView} setShowView={setShowView} />  
      </ControlContainer>

      {renderContent()}
      <div ref={loadMoreRef} />
    </Page>
  )
}

export default OrdinaryGallery