import { useCallback, useMemo } from 'react'
import BigNumber from 'bignumber.js'
import { MaxUint256 } from '@ethersproject/constants'
import { useActiveWeb3React } from 'hooks'
import getOGConfig from 'sdk/nft/og'
import { useAppDispatch } from 'state'
import { fetchOGUserABAsync } from 'state/actions'
import { calculateGasMargin } from '../utils'
import { ApprovalState } from './useApproveCallback'
import { useTokenContract } from './useContract'
import { useSingleCallResult } from '../state/multicall/hooks'

export default function useOGApprove(callback: any): [ApprovalState, () => Promise<void>] {
  const { account } = useActiveWeb3React()

  const dispatch = useAppDispatch()

  const config = getOGConfig()
  
  const ogAddress = config.address

  const payTokenContract = useTokenContract(config.payToken.address)

  const inputs = useMemo(() => [account, ogAddress], [account, ogAddress])

  const allowance = useSingleCallResult(payTokenContract, 'allowance', inputs).result

  const [currentallowance ] = useMemo(() => [allowance ? new BigNumber(allowance.toString()) : undefined], [allowance])

  // check the current approval status
  const approvalState: ApprovalState = useMemo(() => {
    if (!payTokenContract) return ApprovalState.UNKNOWN

    if (!currentallowance) return ApprovalState.UNKNOWN

    // amountToApprove will be defined if currentAllowance is
    return currentallowance.gt(0) ? ApprovalState.APPROVED : ApprovalState.NOT_APPROVED
  }, [payTokenContract, currentallowance])

  const approve = useCallback(async (): Promise<void> => {
    if (approvalState !== ApprovalState.NOT_APPROVED) {
      console.error('approve was called unnecessarily')

      return
    }

    if (!payTokenContract) {
      console.error('pay tonken contract is null')

      return
    }

    if (!ogAddress) {
      console.error('no spender')

      return
    }

    const estimatedGas = await payTokenContract.estimateGas.approve(ogAddress, MaxUint256)

    // eslint-disable-next-line consistent-return
    payTokenContract
           .approve(ogAddress, MaxUint256, { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              const interval = setInterval(async () => {
                dispatch(fetchOGUserABAsync(account)).then(
                  () =>
                  {
                   clearInterval(interval)   

                   callback()     
                  }
                 )
              }, 6000)
      })
      .catch((error: Error) => {
        console.error('Failed to approve token', error)
        throw error
      })
  }, [approvalState, payTokenContract, ogAddress, dispatch, account, callback])

  return [approvalState, approve]
}

 
 
