import { useCallback } from 'react'
import { useAppDispatch } from 'state'
import { useActiveWeb3React } from 'hooks'
import { fetchOGUserNftsAsync } from 'state/actions'
import { useOGContract } from './useContract'
import { calculateGasMargin } from '../utils'   

export default function useOGMint(
  url: string, callback: any
): [ () => Promise<void>] {
  const { account } = useActiveWeb3React()

  const dispatch = useAppDispatch()

  const ogContract = useOGContract()
  
  const onMint = useCallback(async (): Promise<void> => {
    if (!ogContract) {
      console.error('ordinary gallery contract is null')

      return
    }

    const estimatedGas = await ogContract.estimateGas.mint(url)
                                                      
    // eslint-disable-next-line consistent-return
    ogContract
           .mint(url, { gasLimit: calculateGasMargin(estimatedGas) })
           .then(() => {
              const interval = setInterval(async () => {
                dispatch(fetchOGUserNftsAsync(account)).then(
                  () =>
                  {
                   clearInterval(interval)   

                   callback()     
                  }
                 )
              }, 6000)   
      })
      .catch((error: Error) => {
        console.error('Failed to mint nft', error)
        throw error
      })
    
  }, [ogContract, dispatch, account, callback, url])

  return [ onMint ]
}

 
