import React from 'react'
import { useActiveWeb3React } from 'hooks'
import { useFetchOG } from 'state/hooks'
import OrdinaryGallery from './OrdinaryGallery'

const OrdinaryGalleryagent : React.FC = () => {
    const { account } = useActiveWeb3React()

    useFetchOG(account)

    return (
     <>
       <OrdinaryGallery />
     </>
    );
}

export default OrdinaryGalleryagent