import React, { useState, useCallback } from 'react'
import styled from 'styled-components'
import { NftMeta, MediaType } from 'sdk/nft/types'
import getOGConfig from 'sdk/nft/og'
import { Button, Flex, Text, LinkExternal, useModal } from 'uikit'
import { useTranslation } from 'contexts/Localization'
import useToast from 'hooks/useToast'
import { ApprovalState } from 'hooks/useApproveCallback'
import { getBalanceNumber } from 'utils/formatBalance'
import { useOG } from 'state/hooks'
import useOGApprove from 'hooks/useOGApprove'
import UnlockButton from 'components/UnlockButton'
import CreateModal from './CreateModal'

const Action = styled.div`
  padding-top: 16px;
`
interface CreateActionsProps 
{
  account: string,

  getMeta: () => NftMeta & { file: any },

  onCommitSuccess: () => void,

  onApproveSuccess: () => void,

  version: number
}

const CreateActions: React.FC<CreateActionsProps> = ({ account, getMeta, onCommitSuccess, onApproveSuccess }) => {
  const { t } = useTranslation()

  const [requestedApproval, setRequestedApproval] = useState(false)

  const { payToken } = getOGConfig()

  const og = useOG()

  const { userallowance, userbalance, createFee } = og
  
  const allowance = getBalanceNumber(userallowance)
  
   const balance = getBalanceNumber(userbalance)
  
   const fee = getBalanceNumber(createFee)

   const isApproved = account && userallowance && (allowance >= fee)
    
   const enoughBalance = account && userbalance && (balance >= fee)

  const { toastSuccess, toastError } = useToast()

  const approveCallback = () =>
  {
    toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now create your nft!`)}`)
       
    setRequestedApproval(false)

    onApproveSuccess()
  }

  const [ approveState, onApprove ] = useOGApprove(approveCallback)

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)

      await onApprove()

      if (approveState ===  ApprovalState.UNKNOWN) {
        // user rejected tx or didn't go thru
        toastError(
          `${t('Error')}`,
          `${t(`Please try again. Confirm the transaction and make sure you are paying enough gas!`)}`,
        )

        setRequestedApproval(false)
      }  else if (approveState === ApprovalState.APPROVED)
      {
        toastSuccess(`${t('Contract Enabled')}`, `${t(`You can now create your nft!`)}`)
       
        setRequestedApproval(false)
      }

    } catch (e) {
      console.error(e)
    }
  }, [onApprove, approveState, toastSuccess, toastError, t])

  const [onPresentCreateModal] = useModal(<CreateModal getMeta={getMeta} onSuccess={onCommitSuccess}/>)

  const onPresentCreate = () =>
  {
     const current_meta = getMeta()

     const result = checkMeta(current_meta)

     if(!result.success)
     {
       toastError(
         `${t('Error')}`,
         `${t(result.error)}`,
       )
     }

     else 
     {
       onPresentCreateModal();
     }
  }

  interface checkResult
  {
     success: boolean,

     error?: string
  }

  function checkMeta(current_meta: NftMeta) : checkResult
  {
     if(!current_meta.name || current_meta.name.length === 0)
     {
       return {
         success: false,

         error: t('name is necessary')
       }
     } 

     if(!current_meta.desc || current_meta.desc.length === 0)
     {
       return {
         success: false,

         error: t('description is necessary')
       }
     } 

     if(current_meta.mediaType === MediaType.Image)
     {
      if(!current_meta.imageurl || current_meta.imageurl.length === 0)
      {
        return {
          success: false,
 
          error: t('image is necessary')
        }
      } 
     }

     else if(current_meta.mediaType === MediaType.Video)
     {
      if((!current_meta.mkvurl || current_meta.mkvurl.length === 0) && (!current_meta.mp4url || current_meta.mp4url.length === 0))
      {
        return {
          success: false,
 
          error: t('video is necessary')
        }
      } 
     }

     return {
       success: true
     }
  }

  const renderApprovalOrCreateButton = () => {
    return isApproved ? (
      <Button mt="8px" width="100%"
        onClick={onPresentCreate}>
        {t('Create')}
      </Button>
    ) : (
      <Button mt="8px" width="100%" disabled={requestedApproval} onClick={handleApprove}>
        {t('Enable')}
      </Button>
    )
  }

  return (
    <Action>
      { !enoughBalance && (<Flex justifyContent="space-between" alignItems="center" mt='10px' mb='15px'>
        <Text bold textTransform="uppercase" color="failure" fontSize="12px" pr="3px">
         {t('insufficientBalance', { name: payToken.symbol })}
        </Text>
 
        <LinkExternal href="/#/swap" style={{ alignSelf: 'center' }} ml='10px'>
         {t('Buy')} {payToken.symbol}
        </LinkExternal>
      </Flex>)}

      {!account ? <UnlockButton mt="8px" width="100%" /> : renderApprovalOrCreateButton()}
    </Action>
  )
}

export default CreateActions
