import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import {
  Card,
  CardBody,
  Heading,
  Button,
  ChevronUpIcon,
  Text,
  CardFooter
} from 'uikit'
import Loader from 'components/Loader'
import getOGConfig from 'sdk/nft/og'
import { useTranslation } from 'contexts/Localization'
import { useAppDispatch } from 'state'
import { fetchNftMetaAsync } from 'state/actions'
import { useNftMeta } from 'state/hooks'
import OGInfoRow from './OGInfoRow'
import NftMediaPreviewElement from '../../NftPreview/NftMediaPreviewElement'
import history from '../../../routerHistory'

export interface OGNftCardProps {
  nftid: number
  refresh?: () => void
}

const Header = styled(OGInfoRow)`
  min-height: 28px;
`
const Container = styled.div`
  background-color: ${({ theme }) => theme.colors.cardBorder};
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-bottom: 100%;
`
const DetailsButton = styled(Button).attrs({ variant: 'text' })`
  height: auto;
  padding: 16px 24px;

  &:hover:not(:disabled):not(:active) {
    background-color: transparent;
  }

  &:focus:not(:active) {
    box-shadow: none;
  }
`
const InfoBlock = styled.div`
  padding: 24px;
`
const OGNftCardElement: React.FC<OGNftCardProps> = ({ nftid }) => {
  const ogConfig = getOGConfig()

  const nftModeMeta = useNftMeta(ogConfig.address, nftid.toString())

  const [isOpen, setIsOpen] = useState(false)

  const [isloading, setIsLoading] = useState(!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))

  const dispatch = useAppDispatch()

  useEffect(() => {
    if (!nftModeMeta || (!nftModeMeta.meta && !nftModeMeta.loading))
    {  
      dispatch(fetchNftMetaAsync(ogConfig.address, nftid, nftModeMeta?.uri))
     .then(()=>
      {
        setIsLoading(false)
      })
    }
  }, [dispatch, nftModeMeta, ogConfig, nftid, setIsLoading])

  const { t } = useTranslation()

  const Icon = ChevronUpIcon

  const handleClick = async () => {
    setIsOpen(!isOpen)
  }

  const handleNftForSell = () => {
    history.push(`/nftforsell/${ogConfig.address}/${nftid}`)
  }

  return (
    <Card isActive>
      {isloading ? ( <Loader/>) : (<Container onClick={handleNftForSell}>
        <NftMediaPreviewElement meta={nftModeMeta?.meta}/> 
      </Container>) }

      <CardBody>
        <Header>
          <Heading>{nftModeMeta?.meta?.name}</Heading>
        </Header>
      </CardBody>
      <CardFooter p="0">
        <DetailsButton width="100%" endIcon={<Icon width="24px" color="primary" />} onClick={handleClick}>
          {t('Details')}
        </DetailsButton>
        {isOpen && (
          <InfoBlock>
            <Text as="p" color="textSubtle" style={{ textAlign: 'center' }}>
              {nftModeMeta?.meta?.desc}
            </Text>
          </InfoBlock>
        )}
      </CardFooter>
    </Card>
  )
}

const OGNftCard = React.memo(OGNftCardElement, (prev, next) => prev.nftid === next.nftid);

export default OGNftCard
